<template>
  <div>
    <header-title
      class="mb-6"
      :class="{
        'mt-8': $vuetify.breakpoint.smAndDown,
        'mt-n7': this.isRefundOnly && $vuetify.breakpoint.mdAndUp,
        'mt-7': !this.isRefundOnly && $vuetify.breakpoint.mdAndUp,

      }"
    >
      {{ this.GET_A_REFUND }}
    </header-title>
    <v-col
      class="mx-auto text-center hidden-md-and-up"
      style="background-color: #F6F6F6;"
    >
      <v-row class="mt-4 mb-4">
        <v-col class="my-auto">
          <o-text
            xl
            bold
          >
            {{ this.getRefundPrice(item) }}
          </o-text>
          <o-text
            xs
            text-secondary
          >
            {{ ESTIMATED_RETURN }}
          </o-text>
        </v-col>
      </v-row>

      <div style="height: 24px;" class="d-flex align-center justify-center">
        <v-divider class="mx-4 dviderLine" />
        <v-img
          :src="require('@/assets/images/downArrow.png')"
          max-width="16"
          max-height="16"
        />
        <v-divider class="mx-4 dviderLine" />
      </div>

      <v-row class="mt-4 mb-4">
        <v-col class="my-auto">
          <v-img
            v-if="!isGiftReturn"
            :src="require('@/assets/images/payment_card.png')"
            max-width="48"
            max-height="32"
            class="mx-auto mt-3"
          />
          <v-img
            v-else
            :src="require('@/assets/images/gift-card-icon.png')"
            max-width="48"
            max-height="32"
            class="mx-auto mt-3"
          />
          <o-text
            v-if="isGiftReturn"
            class="mt-1"
            sm
            bold
          >
            Gift card
          </o-text>
          <o-text
            v-if="!isGiftReturn"
            class="mt-1"
            sm
            bold
          >
            Original
          </o-text>
          <o-text
            v-if="!isGiftReturn"
            class="mt-n1"
            sm
            bold
          >
            Payment
          </o-text>
        </v-col>
      </v-row>
    </v-col>

    <v-row
      class="mx-auto align-center hidden-sm-and-down"
      style="background-color: #F6F6F6;"
    >
      <v-col class="mt-8 mb-8 ml-4">
        <div>
          <o-text
            class=""
            xl
            bold
          >
            {{ this.getRefundPrice(item) }}
          </o-text>
          <o-text
            text-secondary
            xs
          >
            {{ ESTIMATED_RETURN }}
          </o-text>
        </div>
      </v-col>

      <v-col style="height: 24px;" class="d-flex align-center justify-center">
        <v-img
          :src="require('@/assets/images/rightArrow.png')"
          max-width="16"
          max-height="16"
        />
      </v-col>

      <div class="mt-0 mb-4 mr-n4" style="max-width: 165px">
        <v-row class="">
          <v-col :class="isGiftReturn ? 'mr-n8' : ''">
            <o-text
              v-if="isGiftReturn"
              sm
              bold
            >
              Gift
            </o-text>
            <o-text
              v-if="isGiftReturn"
              class="mt-n1"
              sm
              bold
            >
              Card
            </o-text>
            <o-text
              v-if="!isGiftReturn"
              sm
              bold
            >
              Original
            </o-text>
            <o-text
              v-if="!isGiftReturn"
              class="mt-n1"
              sm
              bold
            >
              Payment
            </o-text>
          </v-col>
          <v-col class="">
            <v-img
              v-if="!isGiftReturn"
              :src="require('@/assets/images/payment_card.png')"
              max-width="48"
              max-height="32"
              class="ml-n6"
            />
            <v-img
              v-else
              :src="require('@/assets/images/gift-card-icon.png')"
              max-width="48"
              height="35"
              class="ml-n6"
            />
          </v-col>
        </v-row>
      </div>
    </v-row>

    <o-text
      :class="{
        'mt-2': $vuetify.breakpoint.smAndDown,
        'mt-6': $vuetify.breakpoint.mdAndUp,
      }"
      xs
      text-secondary
    >
      {{ REFUND_CLEAR_MESSAGE }}
    </o-text>
    <action-button
      @click.stop="updateRefundItem"
      class="mt-6"
      :block="$vuetify.breakpoint.smAndDown"
      primary-color
      large
      :loading="isMapLoading"
      x-large
    >
      {{ NEXT }}
    </action-button>
  </div>
</template>

<script>
import ActionButton from '@/components/atoms/ActionButton.vue'
import OText from '@/components/atoms/OText.vue'
import { EXCHANGE_REFUND, ORDER_LOOKUP, PAGE_TITLES } from 'branding/strings.json'
import { mapGetters } from 'vuex'
import { UPDATE_REFUND_LIST } from '@/store/constants.js'
import HeaderTitle from '../molecules/HeaderTitle.vue'
import { getIsGift } from '@/store/sessionStorage.js'

export default {
  name: 'RefundView',
  components: {
    ActionButton,
    OText,
    HeaderTitle
  },
  computed: {
    ...mapGetters({
      reason: 'returns/reason',
      currentItemIndex: 'returns/currentItemIndex',
      policies: 'returns/policies',
      selectedItems: 'order/selectedItems'
    }),
    item () {
      return this.selectedItems[this.currentItemIndex]
    },
    isGiftReturn () {
      return getIsGift()
    }
  },
  data: () => ({
    GET_A_REFUND: EXCHANGE_REFUND.GET_A_REFUND,
    REFUND_CLEAR_MESSAGE: '',
    ESTIMATED_RETURN: EXCHANGE_REFUND.ESTIMATED_RETURN,
    NEXT: ORDER_LOOKUP.NEXT
  }),
  mounted () {
    document.title = PAGE_TITLES.REFUND
    var refundTimingEstimate = this.policies.refund_timing_estimate ? this.policies.refund_timing_estimate : '5-7'
    this.REFUND_CLEAR_MESSAGE = EXCHANGE_REFUND.REFUND_CLEAR_MESSAGE.replace('$$refundTimingEstimate', refundTimingEstimate)
  },
  methods: {
    updateRefundItem: function (event) {
      this.$store.commit('order/' + UPDATE_REFUND_LIST, { ...this.item, reason: this.reason })
      this.$emit('click', event)
    },
    getRefundPrice (item) {
      const price = item ? item.price * item.quantity : 0
      return `$${Number(price).toFixed(2)}`
    }
  },
  props: {
    isMapLoading: {
      type: Boolean,
      required: true
    },
    isRefundOnly: {
      type: Boolean,
      required: false
    }
  }
}
</script>

<style scoped>

.dviderLine{
  height: 2px;
  color: #545454;
}

</style>
